___CSS_LOADER_EXPORT___.push([module.id, `/* Reset some default styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: rgb(6, 5, 5);
}
h1{
    text-align: center;
    font-size: 4rem;
    margin-top: 10px;
}
main{
    display: flex;
    justify-content: center;
    align-items: center; 
    margin-top: 50px;
    background-color: gray;
    max-width: 400px;
    margin-inline: auto;
    padding: 20px;
    border-radius: 20px;
}
.calculatorContainer{
    width: 350px;
}
.result,.b{
    font-size: 45px;
}
.resultContainer{
    border-radius: 10px;
    min-height: 110px;
    background-color: rgb(187, 186, 186);
    text-align: right;
    display: grid;
    align-content: space-evenly;
    gap: 10px;
    font-weight: 550;
    padding:10px 15px;
    margin-bottom: 20px;
}

.btn-container{
    display: grid;
    grid-template-columns: repeat(4,minmax(70px,1fr));
    gap: 10px;
}
.btn{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px;
    border-radius: 10px;
    border: none;
    font-size: 20px;
    cursor: pointer;
    background-color: rgb(187, 186, 186);
}
.btn-clear,.btn-delete{
    grid-column: span 2;
}
.btn-clear{
    background-color: rgb(218, 124, 124);
}
.btn-delete{
    background-color: rgb(110, 110, 224);
}




